<template>
  <div id="app">
    <notifications group="foo" position="bottom center" max="1"/>
    <div class="score">Score: {{ score }}</div>
    <div v-if="question">
<flashcard :front="question" :back="answers"></flashcard>

    <div v-if="!this.$store.state.cardFlipped">
      <button v-for="answer in answers" :key="answer.text" @click="submit(answer)">
        {{answer.text}} 
      </button>
    </div>
    <div v-else>
      <button @click="flipIfFlipped()">
        Next
      </button>
    </div>

    </div>
    <div v-else>
      Done for today!
    </div>
    
  </div>
</template>

<script>
import flashcard from "./components/Flashcard.vue";
export default {
  components: {
    flashcard
  },
  data() {
    return {
      score: 0
    };
  },
  computed: {
    question() {
      return this.$store.getters.currentQuestion; // handled by vuex
    },
    answer() {
      return this.$store.getters.currentAnswer; // handled by vuex
    },
    answers() {
      return this.$store.getters.currentAnswers; // handled by vuex
    }
  },
  methods: {
    correct() {
      this.$store.dispatch("correctAnswer"); // handled by vuex
      this.score++;
    },
    wrong() {
      this.$store.dispatch("wrongAnswer"); // handled by vuex
    },
    submit(answer) {
      if (answer.correct) {
        this.$store.dispatch("correctAnswer"); // handled by vuex
        this.score++;
        this.$notify({
          group: "foo",
          title: "Good job!",
          type: "success"
        });
      } else {
        this.$store.dispatch("wrongAnswer"); // handled by vuex
        this.$notify({
          group: "foo",
          title: "You'll get there!",
          type: "error"
        });
      }
    },
    flipIfFlipped() {
      this.$store.commit("flipCard"); // handled by vuex
      this.$store.dispatch("init");
    }
  }
};
</script>

